import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class Header extends Component {
    constructor(props) {
        super(props);
        this.changeMenuStatus = this.changeMenuStatus.bind(this);
        this.state = {
            showMenu: false
        };
    }

    // Change the menu status
    changeMenuStatus() {
        this.setState({ showMenu: !this.state.showMenu });
    }

    render() {

        // We check if the user is logged in
        if (!this.props.isLoggedIn) {
            return (
                ""
            );
        }

        //If it's logged in we display the menu
        return (
            <header>
                <img src="images/logo.png" alt="Logo" />
                <ul>
                    <li>
                        <Link className={this.props.current === "asistencia" ? "active" : ""} to="/home" onClick={() => { this.props.changeCurrent("asistencia"); this.changeMenuStatus(); }}>
                            <FontAwesomeIcon icon="clipboard-list" />
                            Asistencia
                        </Link>
                    </li>
                    <li>
                        <Link className={this.props.current === "select-evento-live" ? "active" : ""} to="/select-evento-live" onClick={() => { this.props.changeCurrent("select-evento-live"); this.changeMenuStatus(); }}>
                            <FontAwesomeIcon icon="chart-line" />
                            Estadísticas
                        </Link>
                    </li>
                    <li>
                        <Link className={this.props.current === "select-evento-invitados" ? "active" : ""} to="/select-evento-invitados" onClick={() => { this.props.changeCurrent("select-evento-invitados"); this.changeMenuStatus(); }}>
                            <FontAwesomeIcon icon="clipboard-list" />
                            Invitados
                        </Link>
                    </li>
                </ul>
                <Link className="sign-out" to="/landing" onClick={() => { this.props.changeCurrent("select-evento-live"); this.changeMenuStatus(); this.props.logOff(); }}>
                    <FontAwesomeIcon icon="sign-out" />
                    Salir
                </Link>
            </header>
        );
    }
}
export default Header;