import React, { Component } from 'react';
import Select from 'react-select';
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import QRCode from 'qrcode.react';
import swal from 'sweetalert';

// const customStyles = {
//     control: styles => ({ ...styles, backgroundColor: 'rgba(0,0,0,0.17)', border: 'none', width: '183px', height: "20px" }),
//     placeholder: styles => ({ ...styles, "color": "#ffffff" }),
//     singleValue: styles => ({ ...styles, "color": "#ffffff" }),
//     dropdownIndicator: styles => ({ ...styles, "color": "#ffffff" }),
// }
class Asistencia extends Component {
    constructor(props) {
        super(props);
        this.sendAttendance = this.sendAttendance.bind(this);
        this.saveNewAttendance = this.saveNewAttendance.bind(this);
        this.print = this.print.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleChangeSelect = this.handleChangeSelect.bind(this);
        this.getGuests = this.getGuests.bind(this);
        this.resetState = this.resetState.bind(this);

        this.state = {
            qr: null,
            guests: [{ label: "Cargando", value: 0 }],
            nombre: "",
            nombreO: "",
            apellido: "",
            apellidoO: "",
            email: "",
            emailO: "",
            telefono: "",
            telefonoO: "",
            empresa: "",
            empresaO: "",
            empresas: [{ label: "Cargando", value: 0 }],
            puesto: "",
            puestoO: "",
            segmento: "",
            segmentoO: "",
            puestos: [{ label: "Cargando", value: 0 }],
            codigo: "",
            black: 0
        }


        this.qr = "";
    }

    componentDidMount() {
        this.getGuests();
    }
    componentDidUpdate(prevProps) {
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.getGuests()
        }
    }
    // We send attendance for the current user
    sendAttendance() {
        let t = this;
        let s = this.state;
        let po = null,
            eo = null;
        if (typeof this.state.puestoO !== "undefined") {
            po = this.state.puestoO.value;
        }
        if (typeof this.state.empresaO !== "undefined") {
            eo = this.state.empresaO.value;
        }
        if (s.nombre !== "" && s.apellido !== "" && s.email !== "" && s.telefono !== "") {
            // Get the events
            axios.post(this.props.url + "api/save-attendance", {
                event: this.props.match.params.id,
                guest: this.state.qr.value,
                nombre: this.state.nombre,
                apellido: this.state.apellido,
                email: this.state.email,
                telefono: this.state.telefono,
                empresa: this.state.empresa.value,
                puesto: this.state.puesto.value,
                segmento: this.state.segmento.value,
                segmentoO: this.state.segmentoO.value,
                nombreO: this.state.nombreO,
                apellidoO: this.state.apellidoO,
                emailO: this.state.emailO,
                telefonoO: this.state.telefonoO,
                empresaO: eo,
                puestoO: po
            })
                .then(function () {
                    // t.setState({ qr: null });
                    t.resetState();
                    t.getGuests();
                    swal("Éxito", "El usuario ha sido registrado exitosamente", "success");
                })
                .catch(function (error) {
                    console.log(error);
                });
        } else {
            swal("Error", "Debe de llenar Nombre, Apellido, Email y Teléfono", "error");
        }
    }

    resetState() {
        this.setState({
            qr: null,
            empresas: [{ label: "", value: 0 }],
            puestos: [{ label: "", value: 0 }],
            puesto: "",
            puestoO: "",
            segmento: "",
            segmentoO: "",
            nombre: "",
            apellido: "",
            email: "",
            telefono: "",
            empresa: "",
            empresaO: "",
            codigo: ""
        });
    }

    // We save a user and add attendance for this new user
    saveNewAttendance() {
        let t = this;
        let po = null,
            eo = null;
        let s = this.state;
        if (typeof s.puestoO !== "undefined") {
            po = s.puestoO.value;
        }
        if (typeof s.empresaO !== "undefined") {
            eo = s.empresaO.value;
        }

        if (s.nombre !== "" && s.apellido !== "" && s.email !== "" && s.telefono !== "") {
            // Get the events
            axios.post(this.props.url + "api/save-new-attendance", {
                event: this.props.match.params.id,
                nombre: s.nombre,
                apellido: s.apellido,
                email: s.email,
                telefono: s.telefono,
                empresa: s.empresa.value,
                puesto: s.puesto.value,
                segmento: s.segmento.value,
                segmentoO: s.segmentoO.value,
                nombreO: s.nombreO,
                apellidoO: s.apellidoO,
                emailO: s.emailO,
                telefonoO: s.telefonoO,
                empresaO: eo,
                puestoO: po
            })
                .then(function () {
                    t.resetState();
                    t.getGuests();
                    swal("Éxito", "El usuario ha sido guardado exitosamente", "success");
                })
                .catch(function (error) {
                    console.log(error);
                });
        } else {
            swal("Error", "Debe de llenar Nombre, Apellido, Email y Teléfono", "error");
        }
    }
    // We print the gafete
    print() {
        window.print();
    }

    //############################################################
    handleChangeSelect(option, b) {
        if (b.name === "qr") {
            if (b.action === "clear") {
                this.resetState();
                return;
            }
            if (option) {
                let empresa = null;
                if (this.props.empresas) {
                    empresa = this.props.empresas.findIndex(x => x.value === parseInt(option.empresa_id));
                    empresa = this.props.empresas[empresa];
                }

                let puesto = null;
                if (this.props.puestos) {
                    puesto = this.props.puestos.findIndex(x => x.value === parseInt(option.cs_puesto_id));
                    puesto = this.props.puestos[puesto];
                }

                let segmento = null;
                if (this.props.segmentos) {
                    segmento = this.props.segmentos.findIndex(x => x.value === parseInt(option.cs_segmento_cliente_id));
                    segmento = this.props.segmentos[segmento];
                }

                console.log(option);

                this.setState({
                    [b.name]: option,
                    nombre: option.nombre,
                    apellido: option.apellido,
                    email: option.email,
                    telefono: option.telefono,
                    empresa,
                    puesto,
                    nombreO: option.nombre,
                    apellidoO: option.apellido,
                    emailO: option.email,
                    telefonoO: option.telefono,
                    empresaO: empresa,
                    puestoO: puesto,
                    codigo: option.codigo,
                    segmento: segmento,
                    segmentoO: segmento,
                    black: option.black
                });

            }
        } else {
            this.setState({ [b.name]: option, [b.name + "O"]: option });
        }
    }

    //############################################################
    getGuests() {
        let t = this;
        // Get the events
        axios.post(this.props.url + "api/get-guests", {
            event: this.props.match.params.id
        })
            .then(function (response) {
                t.setState({ guests: response.data });
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    //####################################Handle change####################################
    handleChange = name => event => {
        this.setState({ [name]: event.target.value });
    };

    render() {
        // We get the event information
        let event = null;

        if (this.props.events) {
            event = this.props.events.findIndex(x => x.id === parseInt(this.props.match.params.id));
            event = this.props.events[event];
        }

        let fields = "", badge = "";
        if (event) {
            fields = (
                <React.Fragment>
                    <div className="col">
                        <label>Nombre</label>
                        <input className="input" name="nombre" type="text" placeholder="Nombre" value={this.state.nombre || ""} onChange={this.handleChange('nombre')} />
                    </div>
                    <div className="col">
                        <label>Apellido</label>
                        <input className="input" name="apellido" type="text" placeholder="Apellido" value={this.state.apellido ? this.state.apellido : ""} onChange={this.handleChange('apellido')} />
                    </div>
                    <div className="col">
                        <label>Email</label>
                        <input className="input" name="email" type="text" placeholder="email" value={this.state.email} onChange={this.handleChange('email')} />
                    </div>
                    <div className="col">
                        <label>Teléfono</label>
                        <input className="input" name="telefono" type="text" placeholder="Teléfono" value={this.state.telefono} onChange={this.handleChange('telefono')} />
                    </div>
                    <div className="col">
                        <label htmlFor="empresa">
                            Empresa
                        </label>
                        <Select
                            value={this.state.empresa}
                            onChange={this.handleChangeSelect}
                            name="empresa"
                            options={this.props.empresas}
                            // styles={customStyles}
                            placeholder=""
                        />
                    </div>
                    <div className="col">
                        <label htmlFor="puesto">
                            Puesto
                        </label>
                        <Select
                            value={this.state.puesto}
                            onChange={this.handleChangeSelect}
                            name="puesto"
                            options={this.props.puestos}
                            // styles={customStyles}
                            placeholder=""
                        />
                    </div>
                    <div className="col">
                        <label>Segmento</label>
                        <Select
                            value={this.state.segmento}
                            onChange={this.handleChangeSelect}
                            name="segmento"
                            options={this.props.segmentos}
                            // styles={customStyles}
                            placeholder=""
                        />
                    </div>
                    <div className="col">
                        <button className="print" onClick={this.print}>
                            <FontAwesomeIcon icon="print" />
                            Imprimir Gafete
                        </button>
                        {
                            this.state.qr ?
                                (
                                    <button className="send" onClick={this.sendAttendance}>
                                        <FontAwesomeIcon icon="save" />
                                        Guardar
                                    </button>
                                ) :
                                (
                                    <button className="new" onClick={this.saveNewAttendance}>
                                        <FontAwesomeIcon icon="save" />
                                        Guardar Nuevo Usuario
                                    </button>
                                )

                        }
                    </div>
                </React.Fragment>
            );

            badge = (
                <div className="asistencia gafete">
                    <div className="badge">
                        <div className="big-line">
                            {this.state.nombre} {this.state.apellido}
                        </div>
                        <div className="med-line">
                            {this.state.empresa ? this.state.empresa.label : ""}
                        </div>
                        <div className="qrcode">
                            <QRCode value={this.state.codigo} size={96} />
                        </div>
                        <div className="code">
                            {this.state.codigo}
                        </div>
                    </div>
                </div>
            )
        }
        return (
            <div className="asistencia-container">
                <div className="asistencia">
                    <h1>
                        Asistencia
                    </h1>
                    <div className="row">
                        <div className="col">
                            <label htmlFor="qr">
                                Búsqueda de usuario
                            </label>
                            <Select
                                value={this.state.qr}
                                onChange={this.handleChangeSelect}
                                name="qr"
                                options={this.state.guests}
                                // styles={customStyles}
                                placeholder=""
                                isClearable={true}
                            />
                        </div>
                        <div className="col">
                            <label htmlFor="new">&nbsp;</label>
                            <button className="add" onClick={this.resetState}>
                                <FontAwesomeIcon icon="plus-circle" />
                                Nuevo
                            </button>
                            {
                                this.state.black ?
                                    (
                                        <div className="warning-supervisor">
                                            <FontAwesomeIcon icon="exclamation-triangle" />
                                            Contacte a un supervisor
                                    </div>
                                    ) : ""
                            }
                        </div>

                        {fields}
                    </div>
                </div>

                {badge}
            </div>
        );
    }
}
export default Asistencia;